export default {
	data() {
		return {
			title: 'Hello'
		}
	},
	onLoad(options) {
		for (let key in options) {
			let val = options[key];
			this.initParams += '&' + (key + '=' + val)
		}
		let that = this;

		wx.getSystemInfo({
			success: function(res) {
				if (res.environment != undefined) {
					console.log(res.environment);
					wx.qy.login({
						success: function(res) {
							if (res.code) {
								that.code = res.code;
								that.initParams += ('&code=' + that.code)
								console.log("code:"+that.code);
							} else {
								console.log('获取用户登录态失败！' + res.errMsg)
							}
						}
					});
				} else {
					console.log('非企业微信！');
					wx.login({
						success: function(res) {
							if (res.code) {
								that.code = res.code;
								that.initParams += ('&code=' + that.code)
								console.log("code:"+that.code);
							} else {
								console.log('获取用户登录态失败！' + res.errMsg)
							}
						}
					});
				}
			}
		});

	},
	methods: {
		initRobot() {
			this.$HTTP({
			  method: 'GET',
			  url: '',
			  data: {
				  "sysNum": "15680228584854175",
				  "sourceId": "30373",
				  "s": "p",
			  },
			}).then((res) =>{
			  console.log(res)
			}
			// (err)=>{ console.log(err) }
			)
		},
		gotoChatWithRobot() {
			console.log("this.initParams:"+this.initParams)
			let url = encodeURIComponent(
				"https://demo.faqrobot.net/robot/h5chat.html?sysNum=15680228584854175&receiveId=&sourceId=30373" + this.initParams
			);
			uni.redirectTo({
				url: '../robot/robot?url=' + url
			});
		}
	}
}
